.about-wrapper {
    /* border: 1px solid white; */
    display: flex;
    justify-content: space-between;
    height: 85vh;
    align-items: center;
    width: 75vw;
    margin: auto;
    padding: 0 45px;
}

.about-content {
    /* border: 1px solid yellow; */
    width: 40vw;
}

.about-header {
    color: white;
    font-size: 25px;
    font-family: "SF Mono", "Fira Code", "Fira Mono", "Roboto Mono", "Lucida Console", Monaco, monospace;
    display: flex;
    align-items: center;
    margin-bottom: 40px;
}

.list-style {
    color: rgb(100, 255, 218);
    font-family: "SF Mono", "Fira Code", "Fira Mono", "Roboto Mono", "Lucida Console", Monaco, monospace;
}

.border-line {
    height: 1px;
    background-color: rgb(45, 57, 82);
    width: 30%;
    margin-left: 15px;
    display: inline-block;
}

.about-description {
    color: rgb(165, 162, 162);
    margin-top: 10px;
    font-family: "SF Mono", "Fira Code", "Fira Mono", "Roboto Mono", "Lucida Console", Monaco, monospace;
    margin-bottom: 20px;
    font-size: 18px;
}


.about-image {
    width: 300px;
    height: 300px;

}

.about-image img {
    width: 100%;
    height: 100%;
    object-fit: contain;
}